div.Header {
  padding-top: 1.2em;
  padding-bottom: 1.2em;
  overflow: hidden;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

span.header-options-container {
  margin-left: auto;
}

span.header-options-container > ul.header-options-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  list-style: none;
}

span.header-options-container > ul.header-options-list > li.header-option {
  margin-left: 1.2em;
  color: rgba(255, 255, 255, 0.65);
  cursor: pointer;
}

span.header-options-container > ul.header-options-list > li.header-option:hover {
  color: white;
}


